
body{
    font-family: 'Roboto', sans-serif;
    color: #DCDCDC; 
   
}
html, body {
    width: 100%;
    height: 100vh; /* 100% of the viewport height */
    margin: 0;
  }
.container{
    position: relative;
    width: 450px;
    height: 875px;    
    background: #23252C;
}
p{
    font-size: 15px;
    margin-left: 3px;
}
#resim{
width: 250px;
height: 250px;
background-color: transparent;
background: url("./images/pp.jpg");
background-size: cover;
border-radius: 20px 20px 0px 0px;
}
.info-btns{
    display: flex;
    justify-content: space-around;
}
#mailbtn{
background-image: url("./images/email.png");
background-size:cover;
align-items: center;
position: relative;
width: 100px;
height: 30px;
border-radius: 6px;
}
#linkbtn{
    background-image: url("./images/linkedinButton.png");
    background-size:cover;
    align-items: center;
    position: relative;
    width: 100px;
    height: 30px;
    border-radius: 6px;
}
.info p{
    text-align: center;
    color: #F3BF99;

}
.info h3{
    text-align: center;
}
.footer{
position: absolute;
width: 250px;
height: 64px;
background: #161619;
border-radius: 0px 0px 10px 10px;
justify-content: space-around;
}
.footer img{
    margin-left: 45px;
    margin-top: 20px;
    align-items: center;
}

.bodydiv{
position: absolute;
width: 250px;
margin-top: 30px;
margin-left: 17%;
border-radius: 20px 20px 0px 0px;
background: #1A1B21;
}
.about h5{
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: -8px;
    margin-left: 30px;       
    color: #F5F5F5;
}
.about p{
    margin-left: 30px;  
    margin-right: 15px;     

}
.interests h5{
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    margin-bottom: -8px;
    padding-left: 30px;       
    color: #F5F5F5;
}
.interests p{
    margin-left: 30px;  
    margin-right: 15px;     

}
